@charset "utf-8";
.movies .movie {
    margin-bottom: 70px;
    padding: 20px;
    background-color:white;
    font-weight: 300;
    border-radius:5px;
    color:#adaeb9;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25), 0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movies .movie a {
    display: grid;
    grid-template-columns: minmax(15px, 1fr) 2fr;
    grid-gap: 20px;
    text-decoration: none;
    color: inherit;
}

.movie img {
    position: relative;
    top: -50px;
    width: 100%;
    max-width: 150px;
    margin-right: 30px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25), 0 18px 36px -18px rgba(0,0,0,0.3), 0 -12px 36px -8px rgba(0,0,0,0.25);
}

.movie .movie__title,
.movie .movie__year {
    margin: 0;
    font-weight: 300;
}
.movie .movie__title {
    margin-bottom: 5px;
    font-size: 24px;
    color: #2c2c2c;
}
.movie .movie__genres {
    display: flex;
    flex-wrap: wrap;
    margin: 5px 0;
    padding: 0;
    list-style: none;
}
.movie__genres li,
.movie .movie__year {
    margin-right: 10px;
    font-size: 14px;
}